<template>
    <section>
        <el-dialog :title="param.title" :visible.sync="param.visible" :before-close="closeDialog" class="cds-dialog">
            <div label-width="120">
                <div class="dialog-body">
                    <el-form :model="param.form" ref="addPersonForm" :rules="personFormRules" label-position="left" label-width="80px">
                        <el-form-item label="姓名" prop="contact_name">
                            <el-input v-if="param.type == 'add'" v-model="param.form.contact_name" style="width: 300px"></el-input>
                            <el-input v-else v-model="param.form.contact_name"  disabled style="width: 300px"></el-input>
                            <!--<span class="prompt-warning m-left30">姓名以中英文字符开始，且长度大于2位，小于40的中文、英文字母、数字、"."、下划线组成</span>-->
                        </el-form-item>
                        <el-form-item label="邮箱" prop="contact_email">
                            <el-input v-model="param.form.contact_email" style="width: 300px"></el-input>
                            <el-button type="primary" class="cds-button" v-on:click="sendCode" v-if="!send_status" style="margin-left: 30px">发送验证码</el-button>
                            <el-button v-else type="primary" class="cds-button m-left30" disabled>发送验证码({{ second }}s)</el-button>
                        </el-form-item>
                        <el-form-item label="验证码" prop="code">
                            <el-input v-model.number="param.form.code" style="width: 300px"></el-input>
                            <span class="m-left30" style="font-size: 12px;">请填写邮箱验证码</span>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button class="cds-button" type="primary" v-on:click="addSubmit(true, param.type)">确定</el-button>
                <el-button class="cds-button" v-on:click="closeDialog">取消</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
import { Message } from "element-ui";
import httpServer from "../../service/alarm/alarm_contact_person";
export default {
  name: "add_contact_person",
  props: {
    param: {
      type: Object,
      default: {}
    }
  },
  data() {
    var checkName = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入姓名"));
      } else {
        let result = this.checkName(value);
        if (!result) {
          callback(new Error('姓名以中英文字符开始，且由长度大于2位，小于40的中文、英文字母、数字、"."、下划线组成'));
        } else {
          if (this.param.type == 'add') {
            this.same_personName_status = false;
            this.param.personList.forEach(item => {
              if (item.contact_name == value) {
                this.same_personName_status = true
              }
            });
            if (this.same_personName_status) {
              callback(new Error('联系人姓名已存在'))
            } else {
              this.same_personName_status = false;
              callback()
            }
          } else {
            callback()
          }
        }
      }
    };
    return {
      code: "",
      send_status: false,
      second: 60,
      timer: null,
      personFormRules: {
        contact_name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { validator: checkName, trigger: "blur" }
        ],
        contact_email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          { type: "email", message: "请输入正确的邮箱地址", trigger: ["blur", "change"] }
        ],
        code: [
            { required: true, message: "请输入验证码", trigger: "blur" },
            { type: 'number', message: '请输入六位有效数字', trigger: "blur" }
        ]
      },
      same_personName_status: false
    };
  },
  methods: {
    FnSetEmpty() {
      this.param.visible = false;
      clearInterval(this.timer);
      this.send_status = false;
      this.second = 60;
      this.param.form = {};
      this.$nextTick(() => {
          this.$refs.addPersonForm.resetFields();
      });
    },
    closeDialog() {
      this.FnSetEmpty();
      this.$emit("close");
    },
    sendCode() {
      let reqData = {
        contact_address: this.param.form.contact_email
      };
      this.$refs.addPersonForm.validateField('contact_email', (err) => {
        if (err) {
          console.log(err)
        } else {
          if (this.param.form.contact_email) {
            this.send_status = true;
            this.second = 60;
            window.clearInterval(this.timer);
            if (this.second > 0) {
              this.timer = setInterval(() => {
                var that = this;
                if (that.second > 0) {
                  that.second = that.second - 1;
                } else {
                  that.send_status = false;
                }
              }, 1000);
              this.$once('hook:beforeDestroy', () => {
                clearInterval(this.timer);
              })
            }
            httpServer
              .send_code(reqData)
              .then(response => {
                let data = response.data;
                if (response.code == "Success") {
                  Message({ message: response.msg, type: "success" });
                } else {
                  Message({ message: response.msg, type: "error" });
                }
              })
              .catch(err => {
                  console.log(err);
              });
          }
        }
      })
    },
    addSubmit(type, val) {
      if (type == true) {
        this.$refs.addPersonForm.validate(valid => {
          if (valid) {
            let reqData = {
              contact_name: this.param.form.contact_name,
              contact_email: this.param.form.contact_email,
              verification_code: Number(this.param.form.code)
            };
            if (val == "add") {
              httpServer
                .add_contact_person(reqData)
                .then(response => {
                  let data = response.data;
                  if (data && response.code == "Success") {
                    Message({ message: response.msg, type: "success" });
                    this.FnSetEmpty();
                    if (this.param.goPerson) {
                      this.$router.push('/contact_person');
                    }
                    this.$emit("personSubmitFn", "");
                  } else {
                    Message({ message: response.msg, type: "error" });
                  }
                })
                .catch(err => {
                  console.log(err);
                });
            } else {
              reqData.contact_id = this.param.form.contact_id;
              httpServer
                .update_contact_person(reqData)
                .then(response => {
                  let data = response.data;
                  if (data && response.code == "Success") {
                    Message({ message: response.msg, type: "success" });
                    this.FnSetEmpty();
                    this.$emit("personSubmitFn", "");
                  } else {
                    Message({ message: response.msg, type: "error" });
                  }
                })
                .catch(err => {
                  console.log(err);
                  Message({ message: err.msg, type: "error" });
                });
            }
          }
        });
      } else {
        this.FnSetEmpty();
      }
    },
    checkName(name) {
      let regNumber = /\d+/;
      let regCapital = /[A-Z]+/;
      let regLower = /[a-z]+/;
      let regSpecial = /[._]+/;
      let regChinese = /[\u4E00-\u9FA5]+/;
      let nameSecurityFlag = 0;
      let flag = true;
      if (regNumber.test(name)) {
        // 数字
        nameSecurityFlag++;
      }
      if (regCapital.test(name)) {
        // 大写字母
        nameSecurityFlag++;
      }
      if (regLower.test(name)) {
        // 小写字母
        nameSecurityFlag++;
      }
      if (regSpecial.test(name)) {
        // 特殊符号
        nameSecurityFlag++;
      }
      if (regChinese.test(name)) {
        // 中文
        nameSecurityFlag++;
      }
      if (
        name.length < 2 ||
        name.length > 40 ||
        nameSecurityFlag < 1 ||
        !(
          regChinese.test(name[0]) ||
          regCapital.test(name[0]) ||
          regLower.test(name[0])
        )
      ) {
        flag = false;
      }
      return flag;
    }
  },
  mounted() {
      if (this.timer) {
          clearInterval(this.timer)
      }
  }
};
</script>

